footer {
  width: 100%;
  height: 15rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: var(--warna-biru);
}
@media screen and (min-width: 64rem) {
  footer {
    height: 13rem;
  }
}
footer .footer-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 3rem;
  align-items: center;
  height: 100%;
  max-width: 64rem;
  width: 100%;
}
@media screen and (min-width: 64rem) {
  footer .footer-container {
    padding: 0;
    flex-direction: row;
  }
}
footer .footer-container .sosial-media {
  width: 100%;
}
footer .footer-container .sosial-media h1 {
  color: var(--warna-cream);
}
footer .footer-container .sosial-media .sosial-media-icon {
  max-width: 20rem;
  padding-top: 0.5rem;
  display: flex;
  justify-content: space-between;
}
footer .footer-container .sosial-media .sosial-media-icon a {
  display: flex;
}
footer .footer-container .email {
  padding-top: 2rem;
  width: 100%;
}
@media screen and (min-width: 64rem) {
  footer .footer-container .email {
    padding-top: 0;
  }
}
footer .footer-container .email h1 {
  color: var(--warna-cream);
}
footer .footer-container .email p {
  padding-top: 0.5rem;
  color: var(--warna-cream);
  width: 100%;
}